import NextAuth, { NextAuthOptions } from "next-auth";
import CredentialsProvider from "next-auth/providers/credentials";
import bcrypt from "bcryptjs";
import { prisma } from "@/lib/prisma";

export const authOptions: NextAuthOptions = {
  providers: [
    CredentialsProvider({
      name: "credentials",
      credentials: {
        username: { label: "Username", type: "text" },
        password: { label: "Password", type: "password" },
      },
      async authorize(credentials) {
        console.log("[NextAuth] Authorize called with:", credentials?.username);
        
        if (!credentials?.username || !credentials?.password) {
          console.log("[NextAuth] Missing credentials");
          throw new Error("Username dan password diperlukan");
        }

        try {
          const admin = await prisma.admin.findUnique({
            where: { username: credentials.username },
          });

          console.log("[NextAuth] Admin found:", admin ? "yes" : "no");

          if (!admin) {
            console.log("[NextAuth] Admin not found");
            throw new Error("Username atau password salah");
          }

          const isPasswordValid = await bcrypt.compare(
            credentials.password,
            admin.password
          );

          console.log("[NextAuth] Password valid:", isPasswordValid);

          if (!isPasswordValid) {
            console.log("[NextAuth] Invalid password");
            throw new Error("Username atau password salah");
          }

          console.log("[NextAuth] Login successful for:", admin.username);
          return {
            id: admin.id,
            name: admin.name,
            email: admin.username,
          };
        } catch (error) {
          console.error("[NextAuth] Error during authentication:", error);
          throw error;
        }
      },
    }),
  ],
  session: {
    strategy: "jwt",
    maxAge: 24 * 60 * 60, // 24 hours
  },
  secret: process.env.NEXTAUTH_SECRET || "sakama-super-secret-key-2024",
  pages: {
    signIn: "/admin/login",
  },
  callbacks: {
    async jwt({ token, user }) {
      if (user) {
        token.id = user.id;
      }
      return token;
    },
    async session({ session, token }) {
      if (session.user) {
        session.user.id = token.id as string;
      }
      return session;
    },
  },
};

const handler = NextAuth(authOptions);

export { handler as GET, handler as POST };
